body {
  font-family: 'Lato', sans-serif;
  background-color: #f8f8f8;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}


.container {
  position: absolute;
  /* width: 60%; */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffffff;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  max-height: calc(100vh - 200px); /* Максимальная высота контейнера, учитывая верхние и нижние отступы */
  overflow-y: auto; /* Включение вертикальной полосы прокрутки при необходимости */
  margin: 0 auto; /* Добавляем автоматические отступы по бокам, чтобы центрировать контейнер по горизонтали */
  display: flex;
  flex-direction: column;
  
}

.container h1 {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 23px;
  text-indent: 6px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #2a2e30;
}

.container .content label {
  font-size: 14px;
  color: #2a2e30;
  display: none;
  margin-bottom: 20px;
}

.container .content form {
  position: relative;
  height: auto;
  width: 100%;
  text-align: center;
}

.container .content input.inpt,
.container .content textarea.inpt {
  width: 100%; /* Добавляем это свойство, чтобы элементы занимали всю ширину контейнера */
  font-family: Arial, sans-serif;
  font-size: 14px;
  display: block;
  margin-top: 12px;
  padding: 10px 13px;
  color: #999999;
  outline: none;
  border: 1px solid #d9d9d9;
  background: transparent;
  border-radius: 2px;
  box-sizing: border-box;
}


.container .content textarea.inpt {
  resize: none;
  overflow: hidden;
  height: auto;
}


.container .content input.inpt::-webkit-input-placeholder,
.container .content textarea.inpt::-webkit-input-placeholder {
  font-size: 14px;
  color: #999999;
}

.container .content input.inpt:-moz-placeholder,
.container .content textarea.inpt:-moz-placeholder {
  font-size: 14px;
  color: #999999;
}

.container .content input.inpt::-moz-placeholder,
.container .content textarea.inpt::-moz-placeholder {
  font-size: 14px;
  color: #999999;
}

.container .content input.inpt:-ms-input-placeholder,
.container .content textarea.inpt:-ms-input-placeholder {
  font-size: 14px;
  color: #999999;
}

.container .content input.inpt:focus,
.container .content textarea.inpt:focus {
  border-color: #999999;
}

.drop-zone {
  font-family: Arial, sans-serif;
  font-size: 14px;
  display: block;
  width: 100%;
  color: #999999;
  border: 1px solid #d9d9d9;
  background: transparent;
  border-radius: 2px;
  box-sizing: border-box;
  padding: 40px;
  text-align: center;
  margin-top: 12px;
  position: relative;
  pointer-events: auto; /* Позволяет элементу реагировать на события указателя */
  transition: background-color 0.2s ease; /* Добавляем плавный переход для фона */
}

.drop-zone.drag-over {
  background-color: rgba(0, 0, 0, 0.03); /* Устанавливаем полупрозрачный фон при наведении */
}

#news-image {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
  cursor: pointer; /* Устанавливаем кликабельный курсор для всей области .drop-zone */
}

.drop-zone span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.thumbnail-container,
.preview-images {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 12px;
}

.thumbnail {
  position: relative;
  max-width: 184px;
  max-height: 184px;
  width: auto;
  height: auto;
  margin-left: 7px;
  margin-right: 7px;
  margin-bottom: 12px;
  object-fit: cover;
  border: 1px solid #d9d9d9;
}

.thumbnail img {
  max-width: 100%;
  max-height: 100%;
}


.delete-button {
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.7);
  padding: 5px 6px; /* Уменьшим отступы, чтобы уменьшить размер крестика */
  border-radius: 50%;
  font-size: 14px; /* Уменьшим размер шрифта крестика */
  line-height: 1; /* Чтобы выровнять крестик по вертикали */
}

/* Общие стили для кнопок */
.container .content input.button,
.container .content button {
  font-size: 14px;
  line-height: 38px;
  display: block;
  width: 100%;
  height: 42px;
  margin-top: 12px;
  cursor: pointer;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #2a2e30;
  border: 1px solid #2a2e30;
  background: transparent;
  transition: background-color 0.2s ease, color 0.2s ease;
}
  
.container .content input.button:hover {
  background-color: #2a2e30;
  color: #ffffff;
}

.container .content button.submit:hover {
  background-color: #498f4c;
  color: #ffffff;
}

.container .content button.edit:hover {
  background-color: #777777;
  color: #ffffff;
}

.container .content button.logout:hover {
  background-color: #2a2e30;
  color: #ffffff;
}

.container .content button.delete:hover {
  background-color: #8f4949;
  color: #ffffff;
}

.container .content input:focus {
  outline: none;
}

.container .content .checkbox {
  margin-top: 4px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  border: 0;
  opacity: 0;
  visibility: hidden;
}

.container .content .checkbox + label {
  display: block;
  text-align: left;
  margin-left: 0;
  padding-left: 0;
}

.container .content .checkbox + label:before {
  content: "\A";
  color: #999999;
  font-family: Verdana;
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  border: 1px solid #b5b5b5;
  width: 14px;
  height: 14px;
  margin-right: 5px;
  padding: 0;
}

.container .content .checkbox:checked + label:before {
  content: "✓";
}
