.preloader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #ffffff; /* Цвет фона */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    opacity: 1; /* Начальная непрозрачность */
    transition: opacity 0.5s ease; /* Плавная анимация opacity */
  }
  
  .spinner {
    display: block;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: #75ffba; /* Цвет верхней части прелоадера */
    animation: spin 2s linear infinite;
  }
  
  .spinner:before {
    content: "";
    position: absolute;
    top: 5px;
    left: 5px;
    right: 5px;
    bottom: 5px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: #74fff8;
    animation: spin 3s linear infinite;
  }
  
  .spinner:after {
    content: "";
    position: absolute;
    top: 15px;
    left: 15px;
    right: 15px;
    bottom: 15px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: #85e7ff;
    animation: spin 1.5s linear infinite;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  /* Стили для скрытия прелоадера */
  .preloader.hidden {
    opacity: 0; /* Прозрачность 0 для скрытия */
    pointer-events: none; /* Запрет событий указателя во время скрытия */
  }
  